import React from 'react';
import './about.css';
import perfil from './Foto Perfil.jpeg';
import backgroundcode from './bg.jpg';

function About() {
	return(
		<section id='sobre'>
<main className="sobre">
	<img src={perfil} className="perfil" alt='foto de perfil'/>
	<div className='text'>
	<h2 className='subtitleabout'>Anderson Pignatti</h2>
	<p className='textabout'>
		Sou formado em Técnico em Informática pela Etec Profº Armando Bayeux da Silva. Atualmente cursando o terceiro semestre em Análise de Sistemas pela FAM - Faculdade das Américas. </p>
		<p className='textabout'>Possuo conhecimentos em Front-End nas tecnologias: HTML, CSS, Javascript e atualmente estudando React, sendo esse portfólio meu primeiro projeto nesse framework.</p><p>
		Por ser meu primeiro projeto em React tudo está muito simples, mas graças a esse estudo pude aprender sobre componentes e bibliotecas. Para execução desta página foram utilizados as seguintes biblitecas: Chakra UI, Material UI, e Swiper para realizar o carrossel onde estão demonstrando outros projetos em HTML CSS e Javascript.
		Aos poucos conforme evoluo nos estudos estarei aprimorando este portfólio.
		Atualmente após aprender a utilizar o Swiper estarei estarei aperfeiçoando a responsividade do site e por fim deixar a página de contato funcional.
		</p>
	</div>
</main>
</section>
	)
}


export default About;	